



































import uniStatusBar from '../uni-status-bar/uni-status-bar.vue'
import uniIcon from '../uni-icon/uni-icon.vue'

export default {
	name: 'uni-nav-bar',
	components: {
		uniStatusBar,
		uniIcon
	},
	props: {
		title: {
			type: String,
			default: ''
		},
		leftText: {
			type: String,
			default: ''
		},
		rightText: {
			type: String,
			default: ''
		},
		leftIcon: {
			type: String,
			default: ''
		},
		rightIcon: {
			type: String,
			default: ''
		},
		fixed: {
			type: [Boolean, String],
			default: false
		},
		color: {
			type: String,
			default: '#000000'
		},
		backgroundColor: {
			type: String,
			default: '#FFFFFF'
		},
		statusBar: {
			type: [Boolean, String],
			default: false
		},
		shadow: {
			type: [String, Boolean],
			default: true
		},
		border: {
			type: [String, Boolean],
			default: true
		}
	},
	computed: {
		isFixed() {
			return String(this.fixed) === 'true'
		},
		insertStatusBar() {
			return String(this.statusBar) === 'true'
		},
		hasShadow() {
			return String(this.border) === 'true'
		},
		hasBorder() {
			return String(this.border) === 'true'
		}
	},
	methods: {
		onClickLeft() {
			this.$emit('click-left')
		},
		onClickRight() {
			this.$emit('click-right')
		}
	}
}
